import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';


// VKUI
// Icons
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';

import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
const booksinfo =  ({ id, this: love }) => (
    <Panel id={id}>
        <PanelHeader 
         left={<HeaderButton onClick={() => love.goBack()}><Icon24BrowserBack/></HeaderButton>}
         >
             {love.state.author_info['last_name']}
         </PanelHeader>
        <div className="anotherblock">
          <div className="anotherinfo">
            <div className="anothertitle" >{love.state.author_info['first_name']} {love.state.author_info['middle_name']}</div>
            <div className="anotherbutton" onClick={() => {love.setState({author_id_forshare: love.state.author_info['id']}) ;love.setActiveModal("share_author")}}>
                <img className="anotherbuttondown" src="https://img.icons8.com/ios/50/000000/share.png" alt=""/>
            </div>
            <div className="anotherannotation" >{love.state.author_info['type']}</div>
            <div className="prostoline" ></div>
            <div className="anothermiddletext" >
            {love.state.author_info['biography']}
            </div>
            <div className="prostoline"></div>
            <div className="anotherblockforhard" >
            <div className="anotherhardinfo">: <span>{love.state.author_info['date_of_birth']}</span></div>
            <div className="anotherhardinfo">: <span>{love.state.author_info['date_of_death']}</span></div>
            <div className="anotherhardinfo" > : <span>{love.state.author_info['activity']}</span></div>
            <div className="anotherhardinfo"> : <span>{love.state.author_info['lang']  === "ru" ? "" : ""}</span></div>
            <div className="anotherhardinfo"> :</div>
        {love.state.author_info_result.map(function(result, i) {
            return (
                <div key={i}>
                <div style={{marginLeft: "-15px"}} className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome" >
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
                )
    }) }
    </div>
            </div>
          </div>
    </Panel>
);

export default booksinfo;