import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';


import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

import eruda from 'eruda'
const top = ({id, this: love}) => (
    <Panel id={id}>
        <PanelHeader 
         left={<HeaderButton onClick={() => {love.goBack(); eruda.init()}}><Icon24BrowserBack/></HeaderButton>}
         >
              
         </PanelHeader>
        {love.state.bookstop.map(function(result, i) {
                            return (
                                <div key={i}>
                                    <div className="allbooks">
                                        <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                                        <div onClick={() => love.goBook(result["id"])} className="content">
                                        <div className="contenttitle">{result['name']}</div>
                                        <div className="contentreyt">{result['rating']}</div>
                                        <div className="contentauthorhome">
                                        {result['authors'].map(function(result, i) {
                                return (
                                <span key={i}>
                                {result['first_name']} {result['last_name']}
                                </span>
                                )
                        }) }
                                        </div>
                                        <div className="contentviews">
                                        : {result['genres'].map(function(result, i) {
                                return (
                                <span key={i}>
                                    {result['name']},
                                </span>
                                )
                        }) }
                        <div className="contentviews">
                                            : {result['views']}
                                        </div>
                                        </div>
                                    </div>
                                    <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], book_id_forshare: result['id'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                            </div>
                            </div>
                                )
                    })}
    </Panel>
);


export default top;