import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';

import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
const biography = ({id, this: love}) => (
    <Panel id={id}>
        <PanelHeader 
         left={<HeaderButton onClick={() => love.goBack()}><Icon24BrowserBack/></HeaderButton>}
         >
             
         </PanelHeader>
        {love.state.biography_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goAuthor(result["id"])} alt="" src={result['photo']}/>
                                    <div onClick={() => love.goAuthor(result["id"])} className="content">
        <div onClick={() => love.goAuthor(result["id"])} className="contenttitle">{result['first_name']} {result['last_name']}</div>
                                    <div className="contentauthor">{result['activity']}</div>
                                    <div className="contentviews">
                                        : {result['lang'] === "ru" ? "" : ""}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({authortitle: result['first_name'], author_id_forshare: result['id'], authorlast: result['last_name'], help_id: result['id']}); love.setActiveModal("author")}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
        
    </Panel>
);


export default biography;