import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Select from '@vkontakte/vkui/dist/components/Select/Select';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const genre = ({id, this: love}) => (
    <Panel id={id}>
        <PanelHeader 
         left={<HeaderButton onClick={() => love.goBack()}><Icon24BrowserBack/></HeaderButton>}
         >
             
         </PanelHeader>
<FormLayout>
                <Select
                  top=" "
                  onChange={ ( e ) => {love.setState({genre_result_dop: e.target.value, genre_id_offset: 10}); love.goSelectGenres(e.target.value)}}
                >
                    {love.state.result_genres.map(function(result, i) {
                            return (
                                <option value={result['id']} key={i}>
                                   {result['name']}
                                </option>
                            )
                    })
                    }
                    </Select>
              </FormLayout>
              {love.state.books_genreresult.length === 0 ? <div className="errorsearch">   </div> :
              love.state.books_genreresult.map(function(result, i) {
                            return (
                                <div key={i}>
                                    <div className="allbooks">
                                        <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                                        <div onClick={() => love.goBook(result["id"])} className="content">
                                        <div className="contenttitle">{result['name']}</div>
                                        <div className="contentreyt">{result['rating']}</div>
                                        <div className="contentauthorhome">
                                        {result['authors'].map(function(result, i) {
                                return (
                                <span key={i}>
                                {result['first_name']} {result['last_name']}
                                </span>
                                )
                        }) }
                                        </div>
                                        <div className="contentviews">
                                        : {result['genres'].map(function(result, i) {
                                return (
                                <span key={i}>
                                    {result['name']},
                                </span>
                                )
                        }) }
                        <div className="contentviews">
                                            : {result['views']}
                                        </div>
                                        </div>
                                    </div>
                                    <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                            </div>
                            </div>
                                )
                    })}
                {
                  love.state.genre_result_dop.length >= 10 ? 
                  <Div>
                  <Button  size="xl" level="secondary" onClick={() => {love.setState({genre_id_offset: love.state.genre_id_offset + 10}); love.goSelectGenres_Load(love.state.dop_genre_id)}} style={{ marginRight: 8 }}> </Button>
                  </Div>
                  : null
                  } 
</Panel>
)


export default genre;