	LoadBooks() {
		fetch(this.state.api_url + "method=books.get&offset=0&count=10&" + window.location.search.replace('?', ''))
		.then(res => res.json())
		.then(result => {
			if(result.response) {
				this.setState({listbooks: result.response, listbooks_result: result.response, popout: null})
	        }
			else {
				this.setState({errorcodeforalert: result.error.message});
				this.showZaprosFailedAun();
			} 
		})
	}
	LoadBooks_Load() {
		this.setState({popout: <MySpinner/>})
		this.setState({offset_home: this.state.offset_home + 10})
		fetch(this.state.api_url + "method=books.get&offset=" + this.state.offset_home + "&count=10&" + window.location.search.replace('?', ''))
		.then(res => res.json())
		.then(result => {
			var sliyan = this.state.listbooks.concat(result.response)
			if(result.response) {
				this.setState({listbooks: sliyan, listbooks_result: result.response, popout: null})
	        }
			else {
				this.setState({errorcodeforalert: result.error.message});
				this.showZaprosFailedAun();
			} 
		})
	}
