	goBook(book_id) {
		this.setState({activeModal: null, popout: 
			<MySpinner/>, size: window.pageYOffset, size_two: document.documentElement.clientWidth
		})
		fetch(this.state.api_url + "method=book.getById&book_id=" + book_id + "&" + window.location.search.replace('?', ''))
		.then(res => res.json())
		.then(result => {
			if(result.response) {
				window.history.pushState({panel: 'bookinfo'}, `bookinfo`);
				this.setState({
					selected_book: result.response,
					popout: null, 
					activePanel: "bookinfo",
					history: [...this.state.history, 'bookinfo']
				
				})
				this.setState({selected_book_authors: this.state.selected_book['authors']})
				this.setState({selected_book_authors_fist: this.state.selected_book_authors['0'].first_name,selected_book_authors_two: this.state.selected_book_authors['0'].last_name})
				this.setState({is_favorite: this.state.selected_book['is_favorite']})
				this.setState({is_rated: this.state.selected_book['is_rated'], id_author: result.response.authors['0']['id']})
			}
		})
		.catch(err => {
			this.showZaprosFailedAun();
		});
	}

