	commentsAdd(book_id) {
		this.setState({popout: <MySpinner/>})
		this.setState({activeModal: null})
		if(this.state.reyt === 0) {
			this.setState({errorcodeforalert: "  !"});
			this.showZaprosFailedAun();
		} else {
		fetch(this.state.api_url + "method=comment.add&book_id=" + book_id+ "&rate=" + this.state.reyt + "&comment=" + this.state.commentforrezention_add+ "&" + window.location.search.replace('?', ''))
		.then(res => res.json())
		.then(result => {
			if(result.response) {
				this.setState({activeModal: null, popout: null, is_rated: true})
			}
			else {
				this.setState({errorcodeforalert: result.error.message});
				this.showZaprosFailedAun();
			}
		})
		.catch(err => {
			this.showZaprosFailedAun();
		});
	}
	}
	
