import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const SAVE_TIMEOUT = 300; //   
const BUTTON_SCROLL_Y_OFFSET = 200; //       ,   


class Reader extends React.Component {
    constructor(props) {
        super( props );

        this.state = {
            showReturnButton: false
        };

        this.scroll = 0;
        this.timeout = null;
        this.lastPosition = 0;

        this.returnPosition = this.props.this.state.selected_book.position; //   
    }

    componentDidMount() {
        this.toggleReturnButton();
        window.addEventListener( 'scroll', this.onScroll );
    }
    
    componentWillUnmount() {
        window.removeEventListener( 'scroll', this.onScroll );
    }

    onScroll = () => {
        this.toggleReturnButton();
        if ( window.pageYOffset < this.scroll ) return;

        clearTimeout( this.timeout );
        this.timeout = setTimeout( this.saveVisible, SAVE_TIMEOUT );

        this.scroll = window.pageYOffset;
    }

    isVisible = ( elem ) => {
        let coords = elem.getBoundingClientRect();
        let windowHeight = document.documentElement.clientHeight;
        let topVisible = coords.top > 0 && coords.top < windowHeight;
        let bottomVisible = coords.bottom < windowHeight && coords.bottom > 0;

        return topVisible || bottomVisible;
    }

    getVisibleIndex = () => {
        var reader = document.getElementsByClassName( 'reader__body' )[0];
        var paragraphs = reader.getElementsByTagName( 'p' );

        for ( var i = 0; i < paragraphs.length; i++ ) {
            if ( this.isVisible( paragraphs[i] ) ) return i;
        }

        return 0;
    }

    saveVisible = () => {
        var visibleIndex = this.getVisibleIndex();
        if ( visibleIndex === 0 ) return;

        this.lastPosition = visibleIndex;
        let api_url = "https://api.xelene.me/book/?"
        let book_id = this.props.this.state.selected_book['id']
        fetch(api_url + "method=book.savePosition&book_id=" + book_id+ "&position=" + visibleIndex + "&" + window.location.search.replace('?', ''))
        .then(res => res.json())
		.then(result => {
            console.log("  - " + visibleIndex )
        })
    }

    getPositionTop = ( elem ) => {
        var box = elem.getBoundingClientRect();
        return box.top + window.pageYOffset - 60; // -60      
    }

    returnToPosition = () => {
        var pos = this.returnPosition;
        var reader = document.getElementsByClassName( 'reader__body' )[0];
        var p = reader.getElementsByTagName( 'p' )[pos];
        var scrollTop = this.getPositionTop( p );

        window.scrollTo( 0, scrollTop );
    }

    toggleReturnButton = () => {
        var state = this.returnPosition > 0 && window.pageYOffset < BUTTON_SCROLL_Y_OFFSET;
        this.setState( {
            showReturnButton: state
        } );
    }
    render() {
        return (
            <Panel id={this.props.id}>
                <PanelHeader left={
                    <HeaderButton onClick={() => this.props.this.goBack()}>
                        <Icon24BrowserBack />
                    </HeaderButton>
                }></PanelHeader>

            <div className="hard">
                <div className="text">
                    <div dangerouslySetInnerHTML={{__html: this.props.this.state.booktext}} />
                </div>
            </div>

            {this.state.showReturnButton && <div className="reader__return-button" onClick={this.returnToPosition}> </div> }

            {this.props.this.state.is_rated === false ? 
                <Div>
                    <Button  size="xl" level="secondary" style={{backgroundColor: "#00cbbe", color: "#ffffff"}} stretched onClick={() => this.props.this.setState({activeModal: "modals", commentforrezention_add: ""})}></Button>
                </Div>    
                :null}
                </Panel>
            )
    }
}

export default Reader;